import 'react-native';
import React from 'react';
import {render, screen, fireEvent} from '@testing-library/react-native';
import FloatingButton from '../FloatingButton';
import { shallow } from 'enzyme';

describe('Floating Button', () => {

    it('renders correctly on Detail Screen', () => {
      const {getByText, getByTestId} = render(<FloatingButton  />);
      expect(getByTestId('add')).toBeTruthy();
    });

    it('calls onPress prop when menu icon is pressed', () => {
        const onPress = jest.fn();
        const {getByTestId} = render(<FloatingButton  {...props} />);
        fireEvent.press(getByTestId('add'));
        expect(onPress).toHaveBeenCalled();
        expect(props.navigation.navigate).toHaveBeenCalledWith('Detail', {item: data , screen :'Add Item'});
      });
  


});


